/* Mobile */
@media screen and (max-width: 768px) {
    .Home {
        background-color: #f2e5dd;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #f2e5dd;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .title {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        font-family: 'Karantina';
        font-size: 17vw;
        color: #175a85;
        margin-top: 12vh;
        width: 90%;
        text-align: center;
    }

    .home-text {
        font-family: 'Heebo', sans-serif;
        font-size: 4.7vw;
        color: grey;
        text-align: center;
        position: absolute;
        margin-top: 35vh;
        right: 50%;
        direction: rtl;
        transform: translateX(50%);
        width: 92vw;
    }

    .flexBox-boxes {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        margin-top: 63vh;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
        width: 90vw;
    }

    .boxClose {
        width: 42vw;
        height: auto;
    }

    .flexBox-boxes img:nth-child(1),
    .flexBox-boxes img:nth-child(2) {
        order: 1;
    }

    .flexBox-boxes img:nth-child(3),
    .flexBox-boxes img:nth-child(4) {
        order: 2;
    }

    .flexBox-text {
        position: absolute;
        right: 50%;
        transform: translateX(47%);
        margin-top: 72vh;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        row-gap: 142px;
        column-gap: 110px;
        width: 73vw;
    }


    .flexBox-text span:nth-child(1),
    .flexBox-text span:nth-child(2) {
        order: 1;
    }

    .flexBox-text span:nth-child(3),
    .flexBox-text span:nth-child(4) {
        order: 2;
    }


    .boxText {
        font-family: 'Karantina';
        /* position: absolute; */
        color: white;
        font-size: 9vw;
        /* text-align: center; */
        /* margin-left: -20vh; */
        /* margin-top: 10vh; */
        /* width: 20vw; */
    }

    .SboxClose {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        margin-top: 111vh;
        height: auto;
        width: 88vw;
    }

    .SboxText {
        right: 50%;
        transform: translateX(50%);
        margin-top: 123.3vh;
        margin-right: 2vh;
        font-family: 'Karantina';
        position: absolute;
        color: white;
        font-size: 9vw;
        text-align: center;
    }


    .marginBottom {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        margin-top: 139vh;
        width: 100vw;
        height: 5vh;
        background-color: transparent;
    }

}

